import * as React from 'react';
import marked from 'marked';
import '../github-markdown.css';
import { useData } from '../model';
import { getNoteTitleMatchRegExp, getNoteContextTitlePrefixRegExp } from '../db/note';
import {useHistory} from 'react-router-dom';

const regex = /\[\[([^\]]+)\]\]/ig;

export const MarkedViewer: React.FC<{ markdown: string; contextId: string|null }> = props => {
    const history = useHistory();
    const {data} = useData();

    (window as any).lifeNavigate = function(url: string) {
        history.push(url);
        return false;
    };

    const markdown = props.markdown.replace(regex, (_, group1) => {
        let matchContextId = props.contextId;
        let matchTitle = group1;

        const prefixContext = data.contexts.find(x => getNoteContextTitlePrefixRegExp(x.title).test(group1));
        if (prefixContext) {
            matchTitle = matchTitle.substring(prefixContext.title.length + 1);
            matchContextId = prefixContext.id;
        }

        const referenceRegExp = getNoteTitleMatchRegExp(matchTitle);
        const note = data.notes.find(x => x.contextId === matchContextId && referenceRegExp.test(x.title));

        if (note) {
            const url = `/notes/${note.id}`;
            return `<span style="color: #444">[[ </span><a onclick="return lifeNavigate('${url}')" href="${url}">${group1}</a><span style="color: #444"> ]]</span>`;
        }

        const url = `/notes/new?title=${encodeURIComponent(group1)}${props.contextId ? `&context=${props.contextId}` : ''}`;
        return `<span style="color: #444">[[ </span><a onclick="return lifeNavigate('${url}')" href="${url}">${group1}</a><span style="color: #444"> ? ]]</span>`;
    });

    const html = marked(markdown);
    return (
        <div className="marked-viewer note markdown-body" dangerouslySetInnerHTML={{ __html: html }}></div>
    );
};