import * as React from 'react';
import { useState, useRef } from 'react';
import { classes } from 'typestyle';
import { horizontalSpace } from '../layout/styles';
import { MarkedViewer } from './MarkedViewer';
import AceEditor, { ICommand } from "react-ace";

import {textCompleter, snippetCompleter, keyWordCompleter} from "ace-builds/src-noconflict/ext-language_tools";
import "ace-builds/src-noconflict/mode-markdown";
import "ace-builds/src-noconflict/theme-monokai";
import ReactAce from 'react-ace/lib/ace';
import { Loader } from './Loader';
import { getNoteReferenceMatchRegExp } from '../db/note';
import { useData } from '../model';

interface Node {
    text: string;
    nodes: Node[];
}

export const TreeViewer: React.FC<{ json: string, contextId: string|null, referenceTitle?: string }> = (props) => {
    const [nodes, setNodes] = useState<Node[]|null>(null);
    const [invalid, setInvalid] = useState(false);

    React.useEffect(() => {
        try {
            let nodes = [];
            if (props.json) {
                nodes = JSON.parse(props.json)
            }
            setNodes(nodes);
        } catch {
            setInvalid(true);
        }
    }, [props.json]);

    if (invalid) {
        return <div>Invalid json</div>;
    }

    if (!nodes) {
        return <Loader />;
    }

    const renderNode = (node: Node, index: number, level: number, filter: RegExp|null) => {
        const extra: JSX.Element[] = [];
        if (filter) {
            if (filter.test(node.text)) {
                filter = null;
                if (level > 0) {
                    extra.push(<div key={`extra-${index}-${level}`} style={{paddingTop: 8, color: '#aaa'}}>{'... > '.repeat(level)}</div>)
                }
            } else {
                return (
                    <React.Fragment key={`${level}-${index}`}>
                        {node.nodes.map((n, i) => renderNode(n, i, level + 1, filter))}
                    </React.Fragment>
                );
            }
        }

        return (
            <React.Fragment key={`${level}-${index}`}>
                {extra}
                <div style={{display: 'flex', padding: '.6rem .8rem'}}>
                    <div style={{letterSpacing: '.5rem', color: '#777', lineHeight: 1.5}}></div>
                    <MarkedViewer markdown={node.text} contextId={props.contextId} />
                </div>
                <div style={{borderLeft: '1px solid #555', paddingLeft: 16, marginLeft: 16}}>
                    {node.nodes.map((n, i) => renderNode(n, i, level + 1, filter))}
                </div>
            </React.Fragment>
        );
    };

    const referenceRegExp = props.referenceTitle ? getNoteReferenceMatchRegExp(props.referenceTitle) : null;
    return (
        <div style={{display: 'flex', flexDirection: 'column', justifyContent: 'space-between', height: '100%'}}>
            <div style={{width: '100%'}}>
                {nodes.map((n, i) => renderNode(n, i, 0, referenceRegExp))}
            </div>
        </div>
    );
};

export const TreeEditor: React.FC<{text: string, onTextChanged: (text: string) => void, contextId: string|null}> = (props) => {
    const {data} = useData();
    const [invalid] = useState(() => {
        if (!props.text || props.text === '') {
            return false;
        }
        try {
            JSON.parse(props.text);
            return false;
        } catch {
            return true;
        }
    })

    const [nodes, setNodes] = useState<Node[]>(() => {
        if (invalid) {
            return [];
        }

        if (!props.text || props.text === '') {
            return [
                { text: '', nodes: [] }
            ];
        }

        return JSON.parse(props.text);
    });

    const inputRef = useRef<ReactAce>(null);
    const [active, setActive] = useState(nodes[0]);

    const [noteTitles] = React.useState(() => data.notes.map(n => {
        if (!n.title || !n.title.length) {
            return null;
        }

        if (n.contextId === props.contextId) {
            return n.title;
        }

        const context = data.contexts.find(c => c.id === n.contextId);

        if(!context) {
            return null;
        }

        return `${context.title}/${n.title}`;
    }).filter(t => t !== null) as string[])

    if (invalid) {
        return (
            <div>Invalid tree content (not valid JSON).</div>
        )
    }

    const updateText = () => {
        props.onTextChanged(JSON.stringify(nodes));
    }

    const onInputChanged = (node: Node, value: string) => {
        node.text = value; 
        setNodes([...nodes]);
        updateText();
    };

    const getParentNode = (targetNode: Node, searchNodes: Node[] = nodes, parentNode: Node|null = null): Node|null => {
      for(const node of searchNodes) {
          if (node === targetNode) {
              return parentNode;
          }

          const childResult = getParentNode(targetNode, node.nodes, node);
          if (childResult) {
              return childResult;
          }
      }

      return null;
    };

    const preorderTraversal = () => {
        const result: Node[] = [];
        const stack = [...nodes].reverse();
            
        while(stack.length) {
            let node = stack.pop();

            if (!node) {
                return result;
            }

            result.push(node);

            for(let childNode of [...node.nodes].reverse()) {
                stack.push(childNode);
            }

        }

        return result;
    };

    const insertNode = (afterNode: Node, above = false) => {
        const parentNode = getParentNode(afterNode);
        const searchNodes = parentNode ? parentNode.nodes : nodes;
        const nodeIndex = searchNodes.findIndex(x => x === afterNode);
        const node: Node = { text: '', nodes: [] };
        searchNodes.splice(nodeIndex + (above ? 0 : 1), 0, node);
        setNodes([...nodes]);
        return node;
    };

    const removeNode = (node: Node) => {
        const parentNode = getParentNode(node);
        const searchNodes = parentNode ? parentNode.nodes : nodes;
        const nodeIndex = searchNodes.findIndex(x => x === node);
        searchNodes.splice(nodeIndex, 1);
        setNodes([...nodes]);
        return node;
    };

    const removeNodeIfEmpty = (node: Node) => {
        if (node.text.length || node.nodes.length) {
            return;
        }
        removeNode(node);
    };

    const indentNode = (node: Node) => {
        const parentNode = getParentNode(node);
        const searchNodes = parentNode ? parentNode.nodes : nodes;
        const nodeIndex = searchNodes.findIndex(x => x === node);

        if (nodeIndex === 0) {
            return;
        } else {
            searchNodes.splice(nodeIndex, 1);
            searchNodes[nodeIndex - 1].nodes.push(node);
            setNodes([...nodes]);
        }
    };

    const unindentNode = (node: Node) => {
        const parentNode = getParentNode(node);
        
        if(parentNode === null) {
            return;
        }

        const parentParentNode = getParentNode(parentNode);
        const targetNodes = parentParentNode ? parentParentNode.nodes : nodes;
        const parentNodeIndex = targetNodes.findIndex(x => x === parentNode);
        targetNodes.splice(parentNodeIndex + 1, 0, node);
        const nodeIndex = parentNode.nodes.findIndex(x => x === node);
        parentNode.nodes.splice(nodeIndex, 1);

        setNodes([...nodes]);
    };
    
    const commands: ICommand[] = [
        { name: 'indent', bindKey: { win: 'tab', mac: 'tab' }, exec: () => {
            setTimeout(() => {
                indentNode(active);
            }, 0);
            return true;
        }},
        { name: 'unindent', bindKey: { win: 'shift+tab', mac: 'shift+tab' }, exec: () => {
            setTimeout(() => {
                unindentNode(active);
            }, 0);
            return true;
        }},
        { name: 'enter', bindKey: { win: 'enter', mac: 'enter' }, exec: () => {
            if (active.text === '') {
                return;
            }
            const above = inputRef.current && inputRef.current.editor.getCursorPosition().row === 0 && inputRef.current.editor.getCursorPosition().column === 0;
            setTimeout(() => {
                const newNode = insertNode(active, !!above);
                setActive(newNode);
            }, 0);
            return true;
        }},
        { name: 'up', bindKey: { win: 'up', mac: 'up' }, exec: () => {
            const searchNodes = preorderTraversal();
            let activeIndex = searchNodes.findIndex(x => x === active);
            activeIndex = Math.max(0, activeIndex - 1);
            setTimeout(() => {
                setActive(searchNodes[activeIndex]);
                removeNodeIfEmpty(active);
            }, 0);
            return true;
        }},
        { name: 'down', bindKey: { win: 'down', mac: 'down' }, exec: () => {
            const searchNodes = preorderTraversal();
            let activeIndex = searchNodes.findIndex(x => x === active);
            activeIndex = Math.min(searchNodes.length - 1, activeIndex + 1);
            setTimeout(() => {
                setActive(searchNodes[activeIndex]);
                removeNodeIfEmpty(active);
            }, 0);
            return true;
        }},
        { name: 'space', bindKey: { win: 'space', mac: 'space' }, exec: () => {
            const atStart = inputRef.current && inputRef.current.editor.getCursorPosition().row === 0 && inputRef.current.editor.getCursorPosition().column === 0;
            if (atStart) {
                setTimeout(() => {
                    indentNode(active);
                }, 0);
                return true;
            }
            return false;
        }},
        { name: 'backspace', bindKey: { win: 'backspace', mac: 'backspace' }, exec: () => {
            const atStart = inputRef.current && inputRef.current.editor.getCursorPosition().row === 0 && inputRef.current.editor.getCursorPosition().column === 0;
            if (atStart) {
                setTimeout(() => {
                    unindentNode(active);
                }, 0);
                return true;
            }
            return false;
        }}
    ];

    // Look at https://github.com/ajaxorg/ace/blob/41ac47ec9e26f65fe0ffedeedf14b525a22f5462/lib/ace/ext/language_tools.js#L42-L48
    const completions = {
        getCompletions: (editor: any, session: any, pos: any, prefix: any, callback: CompletionCallback) => {
            // console.log('editor', editor, 'session', session, 'pos', pos, 'prefix', prefix, 'callback', callback);
            const token = session.getTokenAt(pos.row, pos.column);

            if (!token) {
                return;
            }

            const requiredPrefix = '[[';
            const strWithPrefix = token.value.substring(pos.column - prefix.length - requiredPrefix.length);

            if (!strWithPrefix.startsWith(requiredPrefix)) {
                return;
            }

            const completions = noteTitles.map(n => ({
                caption: n,
                snippet: n,
                meta: 'note',
            }))

            callback(null, completions);
        }
    }

    const renderNode = (node: Node, index: number, level: number) => {
        const isActive = node === active;
        return (
            <React.Fragment key={`${level}-${index}`}>
                <div style={{display: 'flex', background: isActive ? '#292929' : undefined, padding: '.6rem .8rem'}} onClick={() => { if(!isActive) { setActive(node); }; }}>
                    <div style={{letterSpacing: '.5rem', color: isActive ? '#078263' : '#777', lineHeight: 1.5}}></div>
                    {isActive 
                        ? <AceEditor
                            key="editor"
                            ref={inputRef}
                            value={node.text} 
                            fontSize={16}
                            style={{background: 'transparent', width: '100%'}}
                            onChange={e => onInputChanged(node, e)} 
                            mode="markdown"
                            theme="monokai"
                            focus
                            highlightActiveLine={false}
                            showGutter={false} 
                            showPrintMargin={false}
                            wrapEnabled={true}
                            maxLines={Infinity}
                            commands={commands}
                            // enableBasicAutocompletion={[snippetCompleter, textCompleter, keyWordCompleter] as any}
                            enableBasicAutocompletion={[completions, snippetCompleter, textCompleter, keyWordCompleter] as any}
                            enableLiveAutocompletion={true}
                            enableSnippets={true}
                            />
                        : <MarkedViewer markdown={node.text} contextId={props.contextId} />}
                </div>
                <div style={{borderLeft: '1px solid #555', marginLeft: 16, paddingLeft: 16}}>
                    {node.nodes.map((n, i) => renderNode(n, i, level + 1))}
                </div>
            </React.Fragment>
        );
    };

    return (
        <>
            <div style={{display: 'flex', background: '#444', position: 'fixed', height: 36, bottom: 0, left: 0, right: 0}} className={classes(horizontalSpace('.5rem'))}>
                    <div className="button" onClick={e => { unindentNode(active); inputRef.current && inputRef.current.editor.focus(); }}>&larr;</div>
                    <div className="button" onClick={e => { indentNode(active); inputRef.current && inputRef.current.editor.focus(); }}>&rarr;</div>
                    <div className="button" onClick={e => { 
                        if (!inputRef.current) {
                            return;
                        }
                        inputRef.current.editor.session.insert(inputRef.current.editor.getCursorPosition(), '[['); 
                        inputRef.current.editor.focus();
                    }}>[[</div>
                    <div className="button" onClick={e => { 
                        if (!inputRef.current) {
                            return;
                        }
                        inputRef.current.editor.session.insert(inputRef.current.editor.getCursorPosition(), ']]'); 
                        inputRef.current.editor.focus();
                    }}>]]</div>
            </div>
            <div style={{height: '100%', marginBottom: 36}}>
                {nodes.map((n, i) => renderNode(n, i, 0))}
            </div>
        </>
    );
};

interface Completion {
    value?: string;
    meta: string;
    type?: string;
    caption?: string;
    snippet?: any;
    score?: number;
    exactMatch?: number;
    docHTML?: string;
}
  
type CompletionCallback = (error: Error|null, results: Completion[]) => void;