import * as React from 'react';
import * as styles from '../layout/styles';
import { classes } from 'typestyle';

export const ToolSection: React.SFC<{ verticalAround?: boolean }> = (props) => {
    let verticalAround = props.verticalAround;

    if (verticalAround === undefined) {
        verticalAround = true;
    }

    return (
        <div className={classes(styles.horizontalSpace('.5rem', true), styles.verticalSpace('.5rem', verticalAround))}>{props.children}</div>
    );
}